.button1 {
    padding: .7rem 1.5rem;
    border: 1px solid #c5c5c5;
    font-family: 'Inter', sans-serif;
    font-size: 1rem;
    font-weight: 500;
    background-color: transparent;
    border-radius: 50px;
    cursor: pointer;
    transition-duration: 200ms;
}

.button1:hover {
    background-color: rgb(0, 61, 41);
    color: white;
}